import React, { Component } from 'react';
import { Map, Marker, GoogleApiWrapper } from 'google-maps-react';

const mapStyles = {
  width: '100%',
  height: '100%'
};

export class SimpleMap extends Component {
  render() {
    return (
        <Map
            google={this.props.google}
            zoom={14}
            style={mapStyles}
            initialCenter={{
            lat: 48.828750,
            lng: 2.119816
            }}
        >
            <Marker position={{ lat: 48.828750, lng: 2.119816}} />
        </Map>
        
    );
  }
}

export default GoogleApiWrapper({
  apiKey: 'AIzaSyDtg8jpNiRlaMkAkItBHgSyQmoDAiLxI7s'
})(SimpleMap);