import React from "react"
import PropTypes from "prop-types"

export default class DirectorDescription extends React.Component {
    constructor(props) {
      super(props);
    }

    display() {
      if(this.props.isShown === true) {
        return (
          <div id="director-description">   
              <p>
              Directeur Général, Hervé Beucherie, Docteur en Pharmacie, peut faire valoir quinze années d’expérience dans le domaine des opérations pharmaceutiques internationales, et définit ainsi la vision de <b>SEPHYTAL PHARMA :</b> 
              </p>
              <p>
              « <b>SEPHYTAL PHARMA</b> a vocation à être proche de ses clients, à comprendre leurs besoins, à mesurer leur satisfaction et à affiner ses solutions. Parce que nous sommes au coeur d'un réseau de compétences techniques, logistiques et réglementaires, et que nous disposons de savoir-faire uniques, nous nous inscrivons comme des partenaires à part entière de nos clients, et comme des acteurs de leur stratégie industrielle et commerciale ». 
              </p>
            </div>
         );
      } else {
        return (
          <div>   
            <h2>Historique</h2>
            <p>
              Créée en 1981, SEPHYTAL a axé sa croissance sur la commercialisation de préparations phytothérapiques, d’extraits végétaux et de produits d’hémi-synthèse pour l’industrie pharmaceutique.
            </p>
            <p>
              En 1989, SEPHYTAL devient leader en France des ventes de poudres laxatives naturelles à base de Séné et de Psyllium.
            </p>
            <p>
              En 1994, SEPHYTAL diversifie son activité dans le domaine des cosmétiques, et devient au début des années 2000 un leader européen des produits d’ethnocosmétologie.
            </p>
            <p>
              En 2006, afin de renforcer et développer l’activité pharmaceutique et chimique en pleine expansion, SEPHYTAL PHARMA voit le jour. Sa création s’accompagne du recrutement de professionnels de la Santé et de spécialistes des opérations internationales pour en faire une société totalement dédiée à l’exploitation des activités chimie et pharmacie dans un environnement mondialisé.
            </p>
          </div>
        
        ); 
      }
    }

   
    render() {
        return ( 
          
               
          <div className="col-lg-8 col-sm-6">
            {this.display()}
          </div>
          
            
        
        );
    }
}

DirectorDescription.propTypes = {
  isShown: PropTypes.bool.isRequired
}
