export const imageData = graphql`
  query {
    images: allFile(filter: {relativePath: {glob: "portfolio/fullsize/*"}}, sort: {fields: name}) {
      edges {
        node {
          childImageSharp {
            fluid {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    products: allFile(filter: {relativePath: {glob: "products/*"}}, sort: {fields: name}) {
      edges {
        node {
          childImageSharp {
            fluid {
              originalImg
            }
          }
        }
      }
    }
    file(relativePath: { eq: "nousSommes/nous_sommes.jpg" }) {
      childImageSharp {
        # Specify the image processing specifications right in the query.
        # Makes it trivial to update as your page's design changes.
        fixed(width: 244, height: 209) {
          ...GatsbyImageSharpFixed
        }
      }
    }
  }
`

